import { RemoveLastDirectoryPartOf } from "../../urlDecoder";
var url = window.location.toString();
function chooseSlideList(slideListId) {
    let requestBody = {
        Id: slideListId
    };
    console.log(requestBody);
    fetch(RemoveLastDirectoryPartOf(url) + "/ChooseSlideList", {
        method: "POST",
        headers: {
            'Content-Type': 'application/json',
            'Accept': 'application/json',
        },
        body: JSON.stringify(requestBody)
    }).then((response) => {
        if (response.status === 200) {
            return response.json();
        }
    }).then((nextSlideData) => {
        if (nextSlideData.url) {
            // Redirect to the URL of the next slide
            window.location.href = nextSlideData.url;
        }
    }).catch(err => {
        console.log("Something went wrong: " + err);
    });
}
document.querySelectorAll('.ChooseSlideList').forEach((btnSubmit) => {
    btnSubmit.addEventListener('click', function () {
        chooseSlideList(Number(btnSubmit.getAttribute('data-info')));
    });
});
